/* GLOBAL */
html {
  font-size: 100% !important; }

body {
  line-height: calc(1.4 * 1.4rem) !important;
  font-size: 1.4rem !important;
  font-weight: 400 !important; }

strong, b {
  font-style: normal;
  font-weight: 700; }

h1, h2 {
  text-transform: uppercase; }

/* HEADER */
.fusion-header {
  border-bottom: #0A5B86 4px solid; }

/* MENU */
.fusion-main-menu > ul > li > a {
  text-transform: uppercase;
  font-weight: 600 !important; }

/* HOMEPAGE */
.tfs-slider .slide-content-container .slide-content {
  padding: 2.0rem;
  background-color: #0A5B86;
  margin-right: 0; }
  .tfs-slider .slide-content-container .slide-content h1 {
    font-family: "soleil";
    font-weight: 600;
    font-size: 1.9em !important;
    line-height: 120% !important;
    text-transform: none; }
  .tfs-slider .slide-content-container .slide-content h2 {
    color: #A2CAF0; }
  .tfs-slider .slide-content-container .slide-content .fusion-button.btn-homepage {
    background-color: #fff; }
    .tfs-slider .slide-content-container .slide-content .fusion-button.btn-homepage .fusion-button-text {
      color: #C17000;
      font-size: 1.6em;
      font-family: "soleil";
      font-weight: 700; }
  .tfs-slider .slide-content-container .slide-content .btn-homepage:hover .fusion-button-text {
    color: #8B5100; }

/* PAGES */
.fusion-page-title-bar .fusion-page-title-wrapper {
  align-items: flex-end; }
  .fusion-page-title-bar .fusion-page-title-wrapper h1.entry-title {
    font-weight: 700;
    letter-spacing: -0.03em; }

.content-margin {
  margin-left: 18.2rem;
  margin-right: 19.2rem; }
